router.beforeEach((to, from, next) => {
    store.commit('setMenu', to.path ==
 '/dashboard');
    if (to.path ==
 '/login') {
        cookie.delCookie('access-1u2s3e4r');
        next();
    } else {
        let loged = cookie.getCookie('access-1u2s3e4r');
        if (!loged) {
            next({
                path: '/login'
            });
        } else {
            next();
        }
    }
});
export default router;



